<template>
  <div class="income-item">
    <div class="income-left">
      <img class="im" :src="order.ogimg" alt="">
    </div>
    <div class="income-center">
      <div>{{order.ogtitle}}</div>
      <div>{{getTime}}</div>
    </div>
    <div class="income-right">￥{{order.ogprice}}</div>
  </div>
</template>

<script>
    import {formatDate} from 'commons/utils'
  export default {
    name: 'IncomeItem',
    props: {
      order: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    computed: {
      getTime() {
        //转换时间
        let date = new Date(this.order.otime*1000);
        return formatDate(date)
      }
    }
  }
</script>

<style scoped>
  .income-item{
    display: flex;
    border-bottom: 1px solid rgba(10,10,10,0.5);
    height: 50px;
    justify-content: space-around;
  }
  .im{
    height: 40px;
    width: 40px;
    margin-top: 5px;
    border-radius: 50%;
  }
  .income-center{
    line-height: 25px;
    font-size: 13px;
  }
  .income-right{
    line-height: 50px;
    color: red;
  }
</style>