<template>
  <div class="income">
    <nav-bar class="nav-barl">
      <div slot="left" class="nav-left" @click="backClick">
        <img src="~assets/img/back.svg" alt="">
      </div>
      <div slot="center" class="nav-titles">每日收入</div>
    </nav-bar>

    <!--显示日期和收入 订单数量-->
    <div class="intitle">
      <input class="intime" type="date" name="itime" v-model="itime" @change="getNewOrders">
      <div class="iprice">收入 ￥{{getAllSale}} 共{{getAllCount}}单</div>
    </div>
    <scroll class="scroll">
      <income-list :orders="saveOrders"></income-list>
    </scroll>
  </div>
</template>

<script>
  import NavBar from 'common/navbar/NavBar'
  import Scroll from 'common/scroll/Scroll'
  import IncomeList from './childcomps/IncomeList'
  import {selOrdersByOtime} from 'network/order'
  export default {
    name: 'Income',
    components: {
      NavBar,
      Scroll,
      IncomeList
    },
    data () {
      return {
        itime: '',
        //保存订单
        saveOrders: []
      }
    },
    computed: {
      //获取价格的和
      getAllSale() {
        return this.saveOrders.reduce((preValue, item) => {
          return preValue + item.ogprice 
        }, 0)
      },
      //获取订单的数量
      getAllCount() {
        return this.saveOrders.length
      }
    },
    created () {
      //获取今天的日期
        let date = new Date();
        //年
        var year=date.getFullYear();
        //月
        var month=date.getMonth()+1;
        //日
        var day=date.getDate();
        if(month < 10) {
          month = '0' + month;
        }
        if(day < 10) {
          day = '0' + day;
        }
        this.itime =  year + '-' + month + '-' + day
        this.selOrdersByOtime()
    },
    methods: {
      backClick() {
        this.$router.back()
      },
      getNewOrders() {
        this.selOrdersByOtime()
      },
      //请求数据
      selOrdersByOtime() {
        //转化时间
        let os = this.itime + " 00:00:00";
        let starttime = os.replace(new RegExp("-","gm"),"/");
        var ot = (new Date(starttime)).getTime() / 1000;

        selOrdersByOtime(ot).then(res => {
          this.saveOrders = res;
        })
      }
    }
  }
</script>

<style scoped>
  .income{
    height: 100vh;
  }
  .nav-barl{
    background-color: #ff8198;
    color: white;
  }
  .nav-left{
    margin-top: 5px;
  }
  .intitle{
    height: 65px;
    background-color: rgba(150,150,150,0.1);
    box-shadow:  0px 2px 2px #ff8198;
  }
  .intime{
    margin-top: 10px;
    margin-left: 5px;
    border: 0;
    background-color: rgba(0,0,0,0);
    outline: none;
  }
  .iprice{
    margin-left: 10px;
    margin-top: 8px;
    font-size: 13px;
  }
  .scroll{
    height: calc(100% - 93px - 65px);
    overflow: hidden;
  }
</style>